.popup {
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    visibility: hidden;
    transition: visibility 0.2s, opacity 0.5s;
    z-index: 2;
    opacity: 0;
}
